
@import url(./assets/minireset.css);
@import url(//fonts.googleapis.com/css?family=Roboto:300,400,700);
.control {
  font-size: 1rem;
  position: relative;
  text-align: left;
}
.input {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border: 1px solid transparent;
  border-radius: 3px;
  box-shadow: none;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  font-size: 1rem;
  height: 2.25em;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  line-height: 1.5;
  padding-bottom: calc(0.375em - 1px);
  padding-left: calc(0.625em - 1px);
  padding-right: calc(0.625em - 1px);
  padding-top: calc(0.375em - 1px);
  position: relative;
  vertical-align: top;
  background-color: white;
  border-color: #dbdbdb;
  color: #363636;
  box-shadow: inset 0 1px 2px rgba(10, 10, 10, 0.1);
  max-width: 100%;
  width: 100%;
}
.input:focus,
.input:active {
  outline: none;
}
.input[disabled] {
  cursor: not-allowed;
}
.input:hover {
  border-color: #b5b5b5;
}
.input:focus,
.input:active {
  border-color: #00d1b2;
}
.field:not(:last-child) {
  margin-bottom: 0.75rem;
}
.label {
  color: #363636;
  display: block;
  font-size: 1rem;
  font-weight: 700;
}
.label:not(:last-child) {
  margin-bottom: 0.5em;
}
.image {
  display: block;
  position: relative;
}
.image img {
  display: block;
  height: auto;
  width: 100%;
}
.image.size-16x16 {
  height: 16px;
  width: 16px;
}
.image.size-24x24 {
  height: 24px;
  width: 24px;
}
.image.size-32x32 {
  height: 32px;
  width: 32px;
}
.image.size-48x48 {
  height: 48px;
  width: 48px;
}
.image.size-64x64 {
  height: 64px;
  width: 64px;
}
.image.size-96x96 {
  height: 96px;
  width: 96px;
}
.image.size-128x128 {
  height: 128px;
  width: 128px;
}
.section {
  background-color: white;
  padding: 3rem 1.5rem;
}
@media screen and (min-width: 1000px) {
.section.medium {
    padding: 9rem 1.5rem;
}
.section.large {
    padding: 18rem 1.5rem;
}
}
.container {
  position: relative;
}
@media screen and (min-width: 1000px) {
.container {
    margin: 0 auto;
    max-width: 960px;
    width: 960px;
}
}
@media screen and (min-width: 1192px) {
.container {
    max-width: 1152px;
    width: 1152px;
}
}
@media screen and (min-width: 1384px) {
.container {
    max-width: 1344px;
    width: 1344px;
}
}
.content {
  color: #4a4a4a;
}
.content:not(:last-child) {
  margin-bottom: 1.5rem;
}
.content li+li {
  margin-top: 0.25em;
}
.content p:not(:last-child),
.content dl:not(:last-child),
.content ol:not(:last-child),
.content ul:not(:last-child),
.content blockquote:not(:last-child),
.content pre:not(:last-child),
.content table:not(:last-child) {
  margin-bottom: 1em;
}
.content h1,
.content h2,
.content h3,
.content h4,
.content h5,
.content h6 {
  color: #363636;
  font-weight: 400;
  line-height: 1.125;
}
.content h1 {
  font-size: 2em;
  margin-bottom: 0.5em;
}
.content h1:not(:first-child) {
  margin-top: 1em;
}
.content h2 {
  font-size: 1.75em;
  margin-bottom: 0.5714em;
}
.content h2:not(:first-child) {
  margin-top: 1.1428em;
}
.content h3 {
  font-size: 1.5em;
  margin-bottom: 0.6666em;
}
.content h3:not(:first-child) {
  margin-top: 1.3333em;
}
.content h4 {
  font-size: 1.25em;
  margin-bottom: 0.8em;
}
.content h5 {
  font-size: 1.125em;
  margin-bottom: 0.8888em;
}
.content h6 {
  font-size: 1em;
  margin-bottom: 1em;
}
.content blockquote {
  background-color: whitesmoke;
  border-left: 5px solid #dbdbdb;
  padding: 1.25em 1.5em;
}
.content ol {
  list-style: decimal outside;
  margin-left: 2em;
  margin-right: 2em;
  margin-top: 1em;
}
.content ul {
  list-style: disc outside;
  margin-left: 2em;
  margin-right: 2em;
  margin-top: 1em;
}
.content ul ul {
  list-style-type: circle;
  margin-top: 0.5em;
}
.content ul ul ul {
  list-style-type: square;
}
.content dd {
  margin-left: 2em;
}
.content pre {
  -webkit-overflow-scrolling: touch;
  overflow-x: auto;
  padding: 1.25em 1.5em;
  white-space: pre;
  word-wrap: normal;
}
.content table {
  width: 100%;
}
.content table td,
.content table th {
  border: 1px solid #dbdbdb;
  border-width: 0 0 1px;
  padding: 0.5em 0.75em;
  vertical-align: top;
}
.content table th {
  color: #363636;
  text-align: left;
}
.content table tr:hover {
  background-color: whitesmoke;
}
.content table thead td,
.content table thead th {
  border-width: 0 0 2px;
  color: #363636;
}
.content table tfoot td,
.content table tfoot th {
  border-width: 2px 0 0;
  color: #363636;
}
.content table tbody tr:last-child td,
.content table tbody tr:last-child th {
  border-bottom-width: 0;
}














/* Buttons
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.button,
button {
  display: inline-block;
  height: 38px;
  padding: 0 30px;
  color: #555;
  text-align: center;
  font-size: 11px;
  font-weight: 600;
  line-height: 38px;
  letter-spacing: .1rem;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border-radius: 4px;
  border: 1px solid #bbb;
  cursor: pointer;
  box-sizing: border-box;
  outline: none;
}
.button:hover,
button:hover,
.button:focus,
button:focus,
{
  color: #333;
  border-color: #888;
  outline: 0;
}
.button.button-primary,
button.button-primary {
  color: #FFF;
  background-color: #33C3F0;
  border-color: #33C3F0;
}
.button.button-primary:hover,
button.button-primary:hover,
.button.button-primary:focus,
button.button-primary:focus,
{
  color: #FFF;
  background-color: #1EAEDB;
  border-color: #1EAEDB;
}



